:root {
  --color-black: #111321;
  --color-white: #dbcccc;
  --color-accent: #4440f6;
  --color-grey: #a8a8a8;
  --font-primary: "Manrope", sans-serif;
  --animation: 2.3s cubic-bezier(.19, 1, .22, 1);
  --padding: 16px;
}

.light {
  --color2: var(--color-main-regular);
}

.logo, .js-menu__link, .menu__link {
  color: inherit;
  text-decoration: none;
}

.js-menu__list, .menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

*, :after, :before {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, pre, samp {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

[type="button"], [type="reset"], [type="submit"], button {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.container {
  padding: 0 var(--padding);
  margin: 0 auto;
}

body {
  color: var(--color-black);
  background: var(--color-white);
  font-family: var(--font-primary);
  letter-spacing: .03em;
  font-size: var(--font-primary-size);
  line-height: 1.19;
}

main {
  margin: 0 auto;
}

.image {
  max-width: 100%;
  height: auto;
  display: block;
}

.icon {
  fill: currentColor;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section {
  margin: 0 auto;
}

.section--top {
  padding-top: 132px;
}

.separator {
  height: 4px;
  width: 100%;
  background: rgba(255, 255, 255, .5);
}

.separator--grey {
  background: var(--color-grey-separator);
}

.separator--with-margin {
  margin-bottom: 30px;
}

.form {
  background: var(--color-grey-bg);
  border-radius: 12px;
  flex-direction: column;
  padding: 30px;
  display: flex;
}

.form--no-radius {
  border-radius: 0;
}

.form__input {
  border: 1px solid var(--color-white);
  color: var(--color-black);
  padding: 15px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.63;
}

.form__input:focus, .form__input:active {
  border-color: var(--color-active-regular);
  outline: none !important;
}

.form__input:invalid {
  border-color: red;
  outline: none !important;
}

.form__input::-ms-input-placeholder {
  color: var(--color-grey-text);
}

.form__input::placeholder {
  color: var(--color-grey-text);
}

.form__input + .form__input, .form__input + .form__textarea {
  margin-top: 15px;
}

.form__textarea {
  border: 1px solid var(--color-white);
  min-height: 130px;
  resize: none;
  color: var(--color-black);
  border-radius: 8px;
  padding: 15px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.63;
}

.form__textarea:focus, .form__textarea:active {
  border-color: var(--color-active-regular);
  outline: none !important;
}

.form__textarea::-ms-input-placeholder {
  color: var(--color-grey-text);
}

.form__textarea::placeholder {
  color: var(--color-grey-text);
}

.form__subtitle {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--color-grey-text);
  margin-top: 30px;
  margin-bottom: 15px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.21;
}

.button {
  color: var(--color-white);
  background-color: var(--color-active-regular);
  border: none;
  border-radius: 12px;
  margin-top: 30px;
  padding: 18px;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  display: block;
  box-shadow: 0 0 20px rgba(36, 163, 255, .5);
}

.button:hover {
  cursor: pointer;
  background-color: var(--color-active-hover);
}

.button:active {
  cursor: pointer;
  background-color: var(--color-active-active);
}

.button--centered {
  margin-bottom: 60px;
  margin-left: auto;
  margin-right: auto;
}

.link {
  color: var(--color-active-regular);
  transition: color var(--animation);
}

.link:hover {
  color: var(--color-accent);
}

