h1, h2, h3, h4, h5, h6, p, ul, address, button {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

li {
  list-style-type: none;
}

.supersoft-bg {
  background: var(--gradient-Supersoft);
  border-radius: 0 0 12px 12px;
  margin-bottom: 80px;
  position: relative;
}

.supersoft-bg:before, .supersoft-bg:after {
  content: "";
  height: 10px;
  border-radius: 0 0 12px 12px;
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.supersoft-bg:before {
  width: calc(100% - 40px);
  background: #cacadb;
  bottom: -10px;
}

.supersoft-bg:after {
  width: calc(100% - 80px);
  background: #e7e7f2;
  bottom: -20px;
}

body.scroll-disable {
  overflow-y: hidden;
}

.header {
  padding: 28px 0;
}

.header__nav {
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

.menu {
  gap: 15px;
  display: none;
}

@media screen and (min-width: 768px) {
  .menu {
    display: flex;
  }
}

.menu__link {
  font-size: 18px;
  font-weight: 700;
  line-height: 1.39;
}

.js-menu {
  width: calc(100vw - 16px);
  transform-origin: bottom;
  z-index: 99;
  transition: transform var(--animation);
  background-color: #729e72;
  border-radius: 12px 12px 0 0;
  padding: 65px 12px 12px;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%)scaleY(0)translateY(100%);
}

.js-menu__list {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.js-menu__link {
  color: var(--color-active-regular);
  background-color: var(--color-grey-bg);
  display: block;
}

.js-menu__close {
  cursor: pointer;
  transition: all var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  position: absolute;
  top: 23px;
  right: 33px;
}

.js-menu__close:hover, .js-menu__close:active {
  background-color: var(--color-grey-separator);
}

.js-menu__close:before, .js-menu__close:after {
  content: "";
  width: 20px;
  height: 2px;
  background-color: var(--color-accent);
  display: block;
  position: absolute;
}

.js-menu__close:before {
  transform: rotate(45deg);
}

.js-menu__close:after {
  transform: rotate(-45deg);
}

.open.js-menu {
  transform: translateX(-50%)scaleY(1)translateY(0);
}

.js-open-menu {
  width: 32px;
  height: 32px;
  transition: background var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 8px;
  flex-direction: column;
  justify-content: space-between;
  margin-left: auto;
  padding: 9px 6px;
  display: flex;
}

.js-open-menu:hover, .js-open-menu:active {
  cursor: pointer;
  background: var(--color-main-alt);
}

.light .js-open-menu:hover, .light .js-open-menu:active {
  background: var(--color-grey-separator);
}

.js-open-menu__line {
  width: 100%;
  height: 2px;
  background-color: var(--color-black);
}

@media screen and (min-width: 768px) {
  .js-open-menu {
    display: none;
  }
}

.modal {
  width: 100%;
  height: auto;
  max-width: 650px;
  background-color: var(--color-grey-bg);
  transition: transform var(--animation);
  position: relative;
  overflow-y: auto;
  transform: scale(1);
}

.modal__close-btn {
  color: #ec6f5d;
  stroke: currentColor;
  transition: background-color var(--animation);
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  display: block;
  position: absolute;
  top: 30px;
  right: 30px;
}

@media screen and (max-width: 319.98px) {
  .modal__close-btn {
    top: 12px;
    right: 12px;
  }
}

.modal__close-btn:hover {
  cursor: pointer;
  background-color: var(--color-grey-separator);
}

.is-hidden .modal {
  transform: scale(.5);
}

.backdrop {
  z-index: 10;
  width: 100vw;
  height: 100vh;
  visibility: visible;
  pointer-events: all;
  transition: opacity var(--animation), visibility var(--animation);
  background-color: rgba(50, 50, 100, .7);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
}

body.modal-open {
  height: 100vh;
  overflow-y: hidden;
}

.logo {
  font-size: 42px;
  font-weight: 700;
  line-height: 57px;
}

.cards {
  background: #f4f4f4;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 32px;
  padding: 50px;
  display: grid;
}

.cards__item {
  width: 395px;
}

.cards__image-wrapper {
  position: relative;
}

.cards__image {
  width: 100%;
  height: 395px;
  object-fit: cover;
  border-radius: 4px;
  display: block;
}

.cards__category {
  text-align: center;
  color: #fff;
  background-color: rgba(68, 64, 246, .698);
  border-radius: 0 4px 4px 0;
  padding: 4px 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  position: absolute;
  top: 40px;
}

.cards__button {
  text-align: center;
  color: #111321;
  cursor: pointer;
  background: #fff;
  border-radius: 20px;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  position: absolute;
  bottom: 12px;
  right: 12px;
}

.cards__details {
  padding-top: 20px;
}

.cards__title {
  letter-spacing: -.01em;
  color: #111321;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
  margin-bottom: 20px;
  font-size: 32px;
  font-weight: 700;
  line-height: 44px;
  display: -webkit-box;
  overflow: hidden;
}

.cards__abstract {
  color: #111321;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 22px;
  display: -webkit-box;
  overflow: hidden;
}

.cards__row {
  justify-content: space-between;
  display: flex;
}

.cards__date {
  text-align: right;
  color: #a8a8a8;
  font-size: 16px;
  line-height: 22px;
}

.footer {
  background-color: var(--color-grey-bg);
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
}

.footer__address-text {
  color: var(--color-main-regular);
  font-style: normal;
  font-weight: 400;
  position: relative;
}

@media screen and (max-width: 319.98px) {
  .footer__address-text {
    margin-bottom: 10px;
  }
}

.footer__address {
  grid-template-columns: 1fr;
  gap: 21px;
  display: grid;
  position: relative;
}

.footer__link {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--color-active-regular);
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.25;
}

.footer__icon {
  margin: 0 auto;
}

@media screen and (max-width: 319.98px) {
  .footer__icon {
    margin-bottom: 10px;
  }
}

.footer__phone {
  color: var(--color-main-alt);
}

@media screen and (max-width: 319.98px) {
  .footer__phone {
    margin-bottom: 10px;
  }
}

.footer__email {
  color: var(--color-active-regular);
}

.footer__copyright {
  color: var(--color-grey-text);
}

.footer__wrap-links {
  list-style-type: none;
}

.carousel {
  position: relative;
}

.carousel.pointer-event {
  touch-action: pan-y;
}

.carousel-inner {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.carousel-inner:after {
  clear: both;
  content: "";
  display: block;
}

.carousel-item {
  float: left;
  width: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  margin-right: -100%;
  transition: transform .6s ease-in-out;
  display: none;
  position: relative;
}

@media (prefers-reduced-motion: reduce) {
  .carousel-item {
    transition: none;
  }
}

.carousel-item.active, .carousel-item-next, .carousel-item-prev {
  display: block;
}

.carousel-item-next:not(.carousel-item-start), .active.carousel-item-end {
  transform: translateX(100%);
}

.carousel-item-prev:not(.carousel-item-end), .active.carousel-item-start {
  transform: translateX(-100%);
}

.carousel-fade .carousel-item {
  opacity: 0;
  transition-property: opacity;
  transform: none;
}

.carousel-fade .carousel-item.active, .carousel-fade .carousel-item-next.carousel-item-start, .carousel-fade .carousel-item-prev.carousel-item-end {
  z-index: 1;
  opacity: 1;
}

.carousel-fade .active.carousel-item-start, .carousel-fade .active.carousel-item-end {
  z-index: 0;
  opacity: 0;
  transition: opacity 0s .6s;
}

@media (prefers-reduced-motion: reduce) {
  .carousel-fade .active.carousel-item-start, .carousel-fade .active.carousel-item-end {
    transition: none;
  }
}

.carousel-control-prev, .carousel-control-next {
  z-index: 1;
  width: 15%;
  color: #fff;
  text-align: center;
  opacity: .5;
  background: none;
  border: 0;
  justify-content: center;
